    get cardsList () {
        let list: CardObject[] = []
        let cards = this.cards
        Object.keys(this.cards).forEach(function(key,index) {
            // key: the name of the object key
            // index: the ordinal position of the key within the object 
            list.push(cards[key])
        });
        return list;
    }

    allData=()=>{

    }
    
    addCards(refName: string, count: number){
        let selected: SingleCardData = this.parent.datamap.cardData[refName];
        selected.count += count;
        this.parent.notify();
    }

    tick=()=>{
        this.deck.tick();
        this.parent.notify();
    }

