import * as React from 'react';
import Engine from './engine/Engine';

export default class FaucetUI extends React.Component < {engine: Engine} , { } > {

    render() {
      let statusStr = 'The faucet is not broken'
      if (this.props.engine.faucet.data.broken === true) statusStr = 'The faucet is broken'

      return (
        <div >
          Tokens: {this.props.engine.datamap.inventory.tokens}<br/>
          Next Token: {this.props.engine.datamap.faucet.remaining}ms<br/>
          {statusStr}  
        </div>
      );
    }
}


