import * as React from 'react';
import Engine from './engine/Engine';
import './App.css'
import SomeCards from './SomeCards';
import { Paper } from '@material-ui/core';
//import styles from './styles';


export default class AllCards extends React.Component < {engine: Engine } , {sorting: string} > {

    sortByName = () => {
      if (this.state.sorting != 'name') {
        this.setState({sorting:'name'})
      } else {
        this.setState({sorting:'none'})
      }
    }

    sortByPower = () => {
      if (this.state.sorting != 'power') {
        this.setState({sorting:'power'})
      } else {
        this.setState({sorting:'none'})
      }
    }


    constructor(props: any){
      super(props);
      this.state= {sorting: 'none'}
    }
    
    render() {
      return (
        <div style={{
          display:'flex', 
          flexWrap:'wrap',
          justifyContent:'center', 
          margin: '10px',
          padding: '10px',
          flexGrow: 0
          }} >
            <Paper>
            <SomeCards cards={this.props.engine.cards.deck.deckCardsList} sorting={'none'}/><br/>
            </Paper>
            <button onClick={this.sortByName}>sort by name</button><br/>
            <SomeCards cards={this.props.engine.cards.cardsList} sorting={this.state.sorting}/>
        </div>
      );
    }
}


