import * as React from "react";

import {BottomNavigation, BottomNavigationAction} from  '@material-ui/core'

import RestoreIcon from '@material-ui/icons/Restore';
import FavoriteIcon from '@material-ui/icons/Favorite';
import LocationOnIcon from '@material-ui/icons/LocationOn';
import CardTravelIcon from '@material-ui/icons/CardTravel';

export interface NavActionData {
    label: string, icon: any, dom?: any
}

export const NavData: NavActionData[] = [
    {label:"Stats", icon:(<RestoreIcon/>),},
    {label:"Faucet", icon:(<FavoriteIcon/>)},
    {label:"Skills", icon:(<LocationOnIcon/>)},
    {label:"Loot", icon:(<CardTravelIcon/>)},
    {label:"Farm", icon:(<CardTravelIcon/>)},
]

export default class BottomNav extends React.Component<{changeTab: any, currentTab: any},{},{}> {
    render () {

        let actions = NavData.map((data, index)=>{
            return (
              <BottomNavigationAction key={data.label} label={data.label} value={index.toString()} icon={data.icon} />
            )
        })

        return (
        <BottomNavigation value={this.props.currentTab} onChange={this.props.changeTab} >
            {actions}
        </BottomNavigation>
        )
    }
}


