import * as React from 'react';
import Engine from './engine/Engine';
import SkillUI from './SkillUI';



export default class AllSkills extends React.Component < {engine: Engine} , { } > {


    render() {

      let skills = this.props.engine.skills;

      return (
        <div >
            <SkillUI skill={skills.clicking}/>
            <SkillUI skill={skills.ticking}/>
            <SkillUI skill={skills.engineering}/>
            <SkillUI skill={skills.farming}/>
        </div>
      );
    }
}


