import * as React from 'react';

import Engine from './engine/Engine';

import ObjectButtons from './ObjectButtons';
import AppBar from '@material-ui/core/AppBar/AppBar';
import { Toolbar, Typography, Button } from '@material-ui/core';

import SimpleMenu from './SimpleMenu';

import './App.css'

import BottomNav from './BottomNav';
import AllSkills from './AllSkills';
import FaucetUI from './FaucetUI';
import AllCards from './AllCards';
import SomeCards from './SomeCards';
import FarmUI from './FarmUI';





class App extends React.Component < {} , {engine: Engine, tab: string} > {

  handleChange = (event: any, value: string) => {
    this.setState({ tab: value });
  }

  constructor(props: any){
    super(props)


    let engine = new Engine();
    engine.setNotify(this.notify)
    this.state = {engine, tab:'1'}
    engine.start();
  }

  notify = () => {
    this.setState({engine: this.state.engine});
  }

  kongLaunch=()=>{
    //console.log(this.kongregate.services.showRegistrationBox)
    //console.log(this.kongregate.services.getUserId())
  }

  public render() {

    return (
      <div className='appWrapper'>
        <div className='headerWrapper'>
        <AppBar position="static">
          <Toolbar>
            <Typography variant="title" color="inherit" >
              LootScape
            </Typography>
            <SimpleMenu title={'Options'}  commands={this.state.engine.menuCommands}/>
            <SimpleMenu title={'Tasks'}  commands={this.state.engine.taskCommands}/>
            <Button id='login' onClick={this.kongLaunch}>
      Sign in/register
    </Button>
          </Toolbar>
        </AppBar>
        </div>
        <div className='commandWrapper'>
          <ObjectButtons commands={this.state.engine.commands}/>
        </div>

        <div className='contentWrapper'>

        
          {this.state.engine.datamap.test.toString()}
          
          {this.state.tab === '0' && <div>
            Ticks: {this.state.engine.datamap.statObjects.ticks.count}<br/>
            Clicks: {this.state.engine.datamap.statObjects.clicks.count}<br/>
            Gold: {this.state.engine.datamap.inventory.gold}<br/>
            Tokens: {this.state.engine.datamap.inventory.tokens} ({this.state.engine.datamap.faucet.remaining})<br/>
            Energy: {this.state.engine.datamap.energy.count}<br/>
            <SomeCards cards={this.state.engine.cards.deck.deckCardsList} sorting='none'/>
          </div>}
          
          {this.state.tab === '1' && <div>
            <FaucetUI engine={this.state.engine}/>       
          </div>}
          
          {this.state.tab === '2' && <div>
            <AllSkills engine={this.state.engine}/>
          </div>}
          
          {this.state.tab === '3' && <div>
          <AllCards engine={this.state.engine}/>
          </div>}

          {this.state.tab === '4' && <div>
          <FarmUI engine={this.state.engine}/>
          </div>}
        
        </div>

        <div className='footerWrapper'>
          <BottomNav changeTab={this.handleChange} currentTab={this.state.tab}/>
        </div>
      
      </div>
    );
  }
}

export default App;



