import Engine from "./Engine";
import { get_random_number } from "./utilities";

export interface FaucetData {
    remaining: number;
    total: number;
    broken: boolean;
    gain: number;
}

export function getFaucetdata() {
    let faucetData: FaucetData = {
        total: 30000, remaining: 30000,
        broken: false,
        gain: 1,
    };
    return faucetData;
}

export default class Faucet {
    
    drip(): any {
        let data = this.data;
        data.remaining = data.total;
        this.parent.datamap.inventory.tokens += data.gain;;
        let rng = get_random_number(0,this.parent.skills.engineering.level);
        if (rng === 0){
            data.broken = true;
        }
    }

    repair =()=> {
        this.data.broken = false;
        this.parent.skills.engineering.gainXP(1);
        this.parent.notify();
    }

    constructor(private parent: Engine) {

    }

    get data (): FaucetData {
        return this.parent.datamap.faucet;
    }

    tick=()=>{
        if (this.data.broken) return;
        let diff = 1000;
        this.data.remaining -= diff;
        if (this.data.remaining <= 0){
            this.drip();
        }
    }
}


