import Engine from "./Engine";

declare var kongregateAPI: any;
declare global {
    interface Window { kongregate: any; }
}


export default class Kong {

    public kongregate: any = null;
    public kongregateAPI: any;

    constructor(public parent: Engine) {
        let setKong = this.setKong;
        this.kongregateAPI = kongregateAPI;
        kongregateAPI.loadAPI(function () {
            window.kongregate = kongregateAPI.getAPI();
            setKong(window.kongregate);
        });

    }

    setKong = (kong: any) => {
        this.kongregate = kong;
        console.log(this.kongregate.services.getUsername());
        this.kongregate.services.addEventListener('login', function () {
            var username = kong.services.getUsername();
            console.log('Kongregate username changed to: ' + username);
       });
    }
    
}




