import Engine from "../Engine";

export interface rcData {

}

export interface unitData {

}

export function getDefaultrcData(): rcData{
    return {}
}

export class rc {
    constructor(private parent: Engine){

    }

    public get data (){
        return this.parent.datamap.rc;
    }
}



export class module {
    constructor(private parent: Engine, private key: string){

    }

    public get data (){
        return this.parent.datamap[this.key];
    }

    public get skills () {
        return this.parent.datamap.skillObjects;
    }
}

class u {
    constructor(public parent: Engine){

    }
}

export class p extends u {
    constructor(public parent: Engine, public index: number){
        super(parent);
    }
}

export class e extends u {
    constructor(public parent: Engine, public index: number){
        super(parent);
    }
}


