import * as React from 'react';
import Skill from './engine/Skill';
import { Button } from '@material-ui/core';



export default class SkillUI extends React.Component < {skill: Skill} , { } > {


    render() {

      let skill = this.props.skill;
       

      return (
        <div >
            {`${skill.refstr}: ${skill.level} ( ${skill.xp} / ${skill.req} )`} 
            <Button variant="contained" color="primary"  onClick={skill.levelUp} >{`Level Up`}</Button>
        </div>
      );
    }
}


