import CoreEngine from "./CoreEngine";
import * as _ from 'lodash'
import Skill from "./Skill";
import {Datamap, newDefaultMap} from './Datamap';
import Faucet from "./Faucet";
import Cards from "./cards/Cards";
import Farm from "./Farm";
import Kong from "./Kong";

export default class Engine extends CoreEngine {

    datamap: Datamap = newDefaultMap();

    prev: number;

    protected tick = (message: MessageEvent) => {
        //console.log('tick');
        let diff = 0;
        if (this.prev === undefined){
            diff = 1000;
        } else {
            diff = message.data - this.prev;
            this.prev = message.data;
        }
        //console.log(diff);
        
        this.datamap.energy.count ++;
        this.tickFunctions.forEach(tickFn => {
            tickFn();
        });
        this.datamap.statObjects.ticks.count ++;
        this.skills.ticking.gainXP(Math.floor(diff/100));
        this.notify();
    }

    faucet: Faucet = new Faucet(this);
    cards: Cards = new Cards(this);
    farm: Farm = new Farm(this);
    kong: Kong = new Kong(this);

    private tickFunctions: VoidFunction[] = [
        this.faucet.tick,
        this.cards.deck.tick,
        this.farm.farmTick,
    ];

    save= () => {
        let datamap = JSON.stringify(this.datamap);
        localStorage.setItem('datamap', datamap);
        this.notify();
    }


    load= () => {
        console.log('loading');
        
        let combstr = localStorage.getItem('datamap');
        if (combstr != null){
            let oldobj = JSON.parse(combstr);
            this.datamap = newDefaultMap();
            _.merge(this.datamap, oldobj)
        }
        this.notify();
    }

    click = () => {
        this.datamap.statObjects.clicks.count++;
        this.datamap.skillObjects.clicking.xp++;
        this.datamap.inventory.gold += this.datamap.skillObjects.clicking.level;
        this.notify();
    }
    
    skills = {
        clicking: new Skill(this, 'clicking'),
        ticking: new Skill(this, 'ticking'),
        engineering: new Skill(this, 'engineering'),
        farming: new Skill(this, 'farming'),
    }

    commands = {
        start: this.start,
        pause: this.pause,
        click: this.click,
    }

    cow=()=>{
        this.datamap.test.push('cow')
        console.log(this.datamap.test);
        this.notify();
        
    }

    bird=()=>{
        this.datamap.test.push('bird')
        console.log(this.datamap.test);
        this.notify();
    }



    menuCommands: [string,VoidFunction][] = [
        ['save',this.save],
        ['load',this.load],

        ['cow',this.cow],
        ['bird',this.bird],
    ]

    get taskCommands () {
        let list: [string,any][]  = []
        if (this.faucet.data.broken) list.push(["Repair Faucet", this.faucet.repair])
        return list
    }

}





