import * as React from 'react';
import Button from '@material-ui/core/Button';
import LinearProgress from '@material-ui/core/LinearProgress';
import { getProgress } from './engine/utilities';
import CardObject from './engine/cards/CardObject';
import { Card, CardContent, CardMedia, Typography, CardActions } from '@material-ui/core';
import './App.css';

export default class CardInterface extends React.Component < {card: CardObject} , { } > {

    componentDidMount(){

    }

    componentWillUnmount(){

    }

    render() {
      let card = this.props.card;
      const upg = card.data.count >= card.req;

      return (
        <Card key={card.aRef.ref + "cont"} className='muiCard'>
          <CardMedia 
            key={card.aRef.name + 'cardmed'}
            image={card.aRef.ref}
            className={'cardMedia'}
            onClick={card.toggleDeck}
          />
          <CardContent style={{padding:0}}>
              <Typography style={{position:'absolute',top:'5px',right:'5px'}}>
                POWER: {card.power}
              </Typography>
              <Typography style={{whiteSpace:'nowrap'}}>
                {card.aRef.name} Lv.{card.data.level}
              </Typography>
              <LinearProgress variant="determinate" color='primary' value={getProgress(card.data.count, 0, card.req)}>
            
              </LinearProgress>
          </CardContent>
          <CardActions style={{padding:0}}>
                <Button className='cardbutton' disabled={!card.canBuy} color='primary' onClick={card.buy1}>
                  Buy 1
                </Button>   
                <Button className='cardbutton' disabled={!upg} color='primary' onClick={card.level}>
                  ^ {card.data.count}/{card.req}
                  </Button>      
          </CardActions>
          </Card> 
      );
    }
}


