import * as React from 'react';
import Button from '@material-ui/core/Button';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

class SimpleMenu extends React.Component < {commands: [string,VoidFunction][], title: string}  , {anchorEl: any} >{

    constructor(props: any){
        super(props)
        this.state = {anchorEl: null}
      }

  handleClick = (event: any) => {
    if (this.props.commands.length)
    this.setState({ anchorEl: event.currentTarget });
  };

  handleClose = () => {
    this.setState({ anchorEl: null });
  };

  render() {
    const { anchorEl } = this.state;
    let hc = this.handleClose;
    let menuItems = this.props.commands.map((value)=>{
      let carryfn = function(){
        hc();
        value[1]();
      }
      return (<MenuItem key={value[0]} onClick={carryfn}>{value[0]}</MenuItem>  )
    })

    return (
      <div>
        <Button 
          color="inherit"
          onClick={this.handleClick}
        >
          {this.props.title} {('Tasks'===this.props.title)&&(<div>{`(${menuItems.length})`}</div>)}
        </Button>
        <Menu
        color="inherit"
          id="simple-menu"
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          onClose={this.handleClose}
        >
          {menuItems}
        </Menu>
      </div>
    );
  }
}

export default SimpleMenu;


