import * as React from 'react';
import { Button } from '@material-ui/core';



export default class ObjectButtons extends React.Component < {commands: any} , { } > {


    render() {

        let buttons: any[] = []
        for (var k in this.props.commands){
          if (this.props.commands.hasOwnProperty(k)) {
              buttons.push(
                <Button key={k} variant="contained" color="primary"  onClick={this.props.commands[k]} >{k}</Button>
              )
          }
        }

      return (
        <div >
              {buttons}
        </div>
      );
    }
}


