import worker from './webworker/do-work'


export default class CoreEngine {

    protected worker: Worker = worker; 
    public notify: VoidFunction;
    
    public setNotify(notify: VoidFunction){
        this.notify = notify;
    }
    
    public start= () => {
        this.worker.postMessage('hello world');   
        this.worker.onmessage = this.tick;
    }

    public pause=()=>{
        this.worker.onmessage = this.dontTick;
    }
    public resume=()=>{
        this.worker.onmessage = this.tick;
    }

    private dontTick= () => {
        console.log('donttick');
       
        //this.notify();
    }

    protected tick= (message: MessageEvent) => {
        console.log('tick');
        this.notify();
    }


}




