import Engine from "../Engine";
import CardObject from "./CardObject";

export default class Deck {

    dList: string[];

    constructor(public parent: Engine, dList?: string[]){
        if (dList){
            this.dList = dList
        } else {
            this.dList = [];
        }
    }

    get deckCardsList () {

        let list: CardObject[] = []
        let dList = this.dList;
        let cards = this.parent.cards.cards
        dList.forEach(function(key,index) {
            let card: CardObject = cards[key];
            // key: the name of the object key
            // index: the ordinal position of the key within the object 
            list.push(card)
        });
        return list;
    }

    get reqEnergy () {
        let list = this.deckCardsList;
        let sum = 0;
        list.forEach(function(card) {
            // key: the name of the object key
            // index: the ordinal position of the key within the object 
            sum += card.data.level;
        });
        return sum;
    }

    tick=()=>{
        let gain = 0;
        let list = this.deckCardsList;
        let energy = this.parent.datamap.energy;
        if (energy.count<this.reqEnergy) {
            this.dList = [];
            return ;
        } else {
            energy.count -= this.reqEnergy;   
            list.forEach(card => {
                gain += card.power
            });
            this.parent.datamap.inventory.gold += gain;
        }
    }
}


