import Engine from "./Engine";

export default class Farm {

    constructor(public parent: Engine){

    }

    get data(): FarmDatamap {
        return this.parent.datamap.farm;
    }

    farmTick=()=>{
        for (let index = 0; index < 10; index++) {
            const plant: growingPlant = this.data['plot'+index];
            if (plant.plantType != 0) plant.timeGrowingMS += 1000;
        }
    }

    plantSeed(plot: number, type: number){
        let chosenPlot: growingPlant = this.parent.datamap.farm['plot'+plot]
        chosenPlot.plantType = type;
        chosenPlot.timeGrowingMS = 0;
    }

    harvestSeed(plot: number){
        let chosenPlot: growingPlant = this.parent.datamap.farm['plot'+plot]
        let plant: plantData = Plants[chosenPlot.plantType]
        
        if (chosenPlot.timeGrowingMS < plant.timeMS) return;
        else {   
            this.parent.datamap.inventory.tokens += plant.value;
            this.parent.datamap.skillObjects.farming.xp += plant.xp;
            chosenPlot.plantType = 0;
            chosenPlot.timeGrowingMS = 0;
        }
    }

}

export interface growingPlant {
    plantType: number,
    timeGrowingMS: number;
}

export interface FarmDatamap {
    plot0: growingPlant;
    plot1: growingPlant;
    plot2: growingPlant;
    plot3: growingPlant;
    plot4: growingPlant;
    plot5: growingPlant;
    plot6: growingPlant;
    plot7: growingPlant;
    plot8: growingPlant;
    plot9: growingPlant;
}

export function getDefaultFarmData(): FarmDatamap {
    return {
        plot0: {plantType: 0, timeGrowingMS: 0},
        plot1: {plantType: 0, timeGrowingMS: 0},
        plot2: {plantType: 0, timeGrowingMS: 0},
        plot3: {plantType: 0, timeGrowingMS: 0},
        plot4: {plantType: 0, timeGrowingMS: 0},
        plot5: {plantType: 0, timeGrowingMS: 0},
        plot6: {plantType: 0, timeGrowingMS: 0},
        plot7: {plantType: 0, timeGrowingMS: 0},
        plot8: {plantType: 0, timeGrowingMS: 0},
        plot9: {plantType: 0, timeGrowingMS: 0},
    }
}

export interface plantData {
    cost: number;
    value: number;
    name: string;
    timeMS: number;
    xp: number;
}

const blue: plantData = {
    cost: 1,
    value: 2,
    name: 'Blue Seed',
    timeMS: 60 * 1000,
    xp: 10
}

const Plants = {
    1: blue
}




