import { ArtRef } from "./types";
import Engine from "../Engine";
import { SingleCardData } from "./cardDatamap";

export default class CardObject {
    basePower: number;

    constructor(public aRef: ArtRef, public parent: Engine){
        let sum = 0;
        let name = aRef.name;
        for (let i = 0; i < name.length; i++){
            sum += name.charCodeAt(i);
        }   
        
        this.basePower = Math.floor(Math.sqrt(sum/10)); 
    }

    get data (): SingleCardData {
        return this.parent.datamap.cardData[this.aRef.name]
    }

    get req (): number {
        if (this.data.level === 0) return 1;
        else return this.data.level * 10
    }

    add=(count: number)=>{
        this.data.count += count;
        this.parent.notify();
    }

    get canBuy () {
        return this.parent.datamap.inventory.tokens > 0;
    }

    buy1 = () =>{
        if (!this.canBuy) return;
        this.data.count ++;
        this.parent.datamap.inventory.tokens --;
        this.parent.notify();   
    }

    add1=()=>{
        this.data.count ++;
        this.parent.notify();
    }

    toggleDeck=()=>{
        let avail = this.data.level > 0;
        if (!avail) return;

        let dList = this.parent.cards.deck.dList;
        let name = this.aRef.name;
        let index = dList.indexOf(name)

        if (index >=0) {
            dList.splice(index, 1)
        } else {
            dList.push(name)
        }

        this.parent.notify();
    }

    level=()=>{
        if (this.data.count < this.req) return;
        else {
            this.data.count -= this.req;
            this.data.level ++
        }
        this.parent.notify();
    }

    get power ()  {
        let base = this.basePower;
        let lv = Math.max(this.data.level, 1);
        let grown = Math.floor(base * Math.pow(1.1, lv-1))
        return grown;
    }
}


