import * as React from 'react';
import CardObject from './engine/cards/CardObject';
import './App.css'
import CardInterface from './CardInterface';
//import styles from './styles';


export default class SomeCards extends React.Component < {cards: CardObject[],sorting: string},{} > {

    listToDoms=(list: CardObject[])=>{
      if (this.props.sorting === 'name'){
        list = list.sort((a: CardObject, b: CardObject)=>{
          return a.aRef.name.localeCompare(b.aRef.name);
        }) 
      }

      if (this.props.sorting === 'power'){
        list = list.sort((a: CardObject, b: CardObject)=>{
          return 1;
        }) 
      }

      return list.map(function(card: CardObject, index){
        return <CardInterface key={card.aRef.name} card={card}/>
      }) 
    }


    constructor(props: any){
      super(props);
      this.state= {sorting: 'none'}
    }
    
    render() {
      return (
        <div style={{
          display:'flex', 
          flexWrap:'wrap',
          justifyContent:'center', 
          margin: '10px',
          padding: '10px',
          flexGrow: 0
          }} >
            {this.listToDoms(this.props.cards)}
        </div>
      );
    }
}


