import {SkillData} from './Skill'
import {getDefaultrcData,rcData} from './rc'
import { FaucetData, getFaucetdata } from './Faucet';
import { LootStoreData, getLootStoreData } from './LootStore';
import { AllCardData, getDefaultAllCardData } from './cards/cardDatamap';
import { FarmDatamap, getDefaultFarmData } from './Farm';

export interface Datamap {
    statObjects: {
        clicks: {count: number};
        ticks: {count: number};
    };
    skillObjects: {
        clicking: SkillData;
        ticking: SkillData;
        engineering: SkillData;
        farming: SkillData;
    };
    inventory: {
        gold: number;
        tokens: number;
    }
    test: string[];
    rc: rcData;
    time: number;
    faucet: FaucetData;
    lootStore: LootStoreData;
    cardData: AllCardData;
    energy: {count: number};
    farm: FarmDatamap;
}

export function newDefaultMap () {
    let map: Datamap = {
        statObjects: {
            clicks: {count: 0},
            ticks: {count: 0}
        },
        skillObjects: {
            clicking: {xp: 0, level: 1},
            ticking: {xp: 0, level: 1},
            engineering: {xp: 0, level: 1},
            farming: {xp: 0, level: 1},
        },
        inventory : {
            gold: 0,
            tokens: 5
        },
        test: [],
        rc: getDefaultrcData(),
        time: Date.now(),
        faucet: getFaucetdata(),
        lootStore: getLootStoreData(),
        cardData: getDefaultAllCardData(),
        energy: {count: 0},
        farm: getDefaultFarmData(),
    }
    return map;
}


